<template>
  <div class="index mescroll">
    <div class="search">
      <form action="/">
        <van-search v-model="value" show-action action-text="搜索" @cancel="onSearch" placeholder="搜索课程名" background="#fff" @search="onSearch" @blur="blur" />
      </form>
    </div>
    <div class="banner">
      <van-swipe :autoplay="3000" indicator-color="white">
        <van-swipe-item v-for="(item,index) in bannerList" :key="index">
          <img :src="$utils.dealImg(item.bimg)" alt />
        </van-swipe-item>
      </van-swipe>
    </div>
    <div class="index-classify">
      <grid :grid-array="cateList" width="55px" height="55px" font-color="#666" borderRadius="50%" @goRouter="goRouter" />
    </div>
    <div class="mid">
      <div class="mid-content">
        <div class="mid-top">
          <p class="title">最新|大V直播</p>
          <p class="more" @click="toLiveMore">查看更多</p>
        </div>
        <div class="list">
          <div class="item" v-for="(item,index) in liveList" :key="index" @click="goLive(item.acode)" v-if="index < 2">
            <img :src="item.aimg?$utils.dealImg(item.aimg):require('@/assets/img/default/defaultH.png')" alt />
            <div class="content">
              <p class="name">{{item.viewName}}</p>
              <div class="flex">
                <div class="labels">
                  <div class="label">直播</div>
                </div>
                <div class="text">{{item.aoutPrice?item.aoutPrice.toFixed(2):'免费'}}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="mid-content">
        <div class="mid-top">
          <p class="title">推荐课程</p>
          <p class="more" @click="toMore">查看更多</p>
        </div>
        <div class="list1">
          <div class="item" v-for="(item,index) in learnList" :key="index" :style="index%2==0?'margin-right:10px':''" @click="goDetail(item)">
            <img :src="item.aimg?$utils.dealImg(item.aimg):require('@/assets/img/default/defaultH.png')" alt />
            <div class="content">
              <p class="name">{{item.viewName}}</p>
              <div class="flex">
                <div class="left">{{item.aoutPrice?`${item.asaleNum}人开通`:`${item.avisitNum}人浏览`}}</div>
                <div class="right">{{item.aoutPrice?'￥' + item.aoutPrice.toFixed(2) : '免费'}}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Search, Swipe, SwipeItem } from "vant";
import Grid from "@/components/Grid";
import { getcollegeIndex } from "@/api";
export default {
  components: {
    [Search.name]: Search,
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    Grid,
  },
  data() {
    return {
      value: "",
      bannerList: [],
      cateList: [],
      learnList: [],
      liveList: [],
    };
  },
  methods: {
    blur() {
      document.body.scrollTop = 0;
    },
    onSearch() {
      this.$router.push({
        name: "collegeCourse",
        query: {
          value: this.value,
        },
      });
    },
    goRouter(item) {
      this.$router.push({
        name: "collegeCourse",
        query: {
          cateCode: item.cateCode,
        },
      });
    },
    toLiveMore() {
      this.$router.push({
        name: "liveList",
      });
    },
    goLive(acode) {},
    toMore() {
      this.$router.push({
        name: "collegeCourse",
      });
    },
    goVideo() {
      this.$router.push({
        name: "videoDetail",
      });
    },
    goDetail(item) {
      sessionStorage.setItem("acode", item.acode);
      this.$router.push({
        name: "courseDetail",
      });
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      getcollegeIndex().then(res => {
        loading.clear();
        if (res.code == 0) {
          this.bannerList = res.data.bannerList;
          this.cateList = res.data.cateList;
          this.cateList.map(item => {
            item.icon = item.cateImg;
          });
          this.learnList = res.data.learnList;
          this.liveList = res.data.liveList;
        } else {
          $this.$toast.fail(res.errorMsg);
        }
      });
    },
  },
  mounted() {
    document.title = "商学院";
    this.loadData();
  },
};
</script>
<style lang="scss" scoped>
.index {
  height: calc(100vh - 50px);
  .banner {
    .van-swipe-item {
      background: #fff;
      img {
        width: 100%;
      }
    }
  }
  .mid {
    .mid-content {
      background: #fff;
      padding: 10px;
      margin: 10px 0 0;
      .mid-top {
        display: flex;
        align-items: center;
        line-height: 30px;
        font-size: 14px;
        .title {
          font-weight: bold;
          flex: 2;
        }
        .more {
          flex: 1;
          text-align: right;
          color: #999;
        }
      }
      .list {
        .item {
          margin: 5px 0 0;
          img {
            width: calc(100% + 2pt);
            border-radius: 10px 10px 0 0;
            display: block;
          }
          .content {
            box-sizing: border-box;
            border: 1pt solid #e2dfe3;
            border-top: 0;
            border-radius: 0 0 10px 10px;
            padding: 10px;
            .name {
              font-size: 15px;
              width: 100%;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
            }
            .flex {
              display: flex;
              align-items: center;
              padding: 10px 0;
              .labels {
                flex: 2;
                .label {
                  border: 1pt solid #000;
                  display: inline-block;
                  font-size: 13px;
                  padding: 0 10px;
                  line-height: 20px;
                  border-radius: 10px;
                }
              }
              .text {
                flex: 1;
                text-align: right;
                color: #9e1435;
                font-size: 13px;
              }
            }
          }
        }
      }
      .list1 {
        .item {
          display: inline-block;
          width: calc((100% / 2) - 5px);
          margin: 5px 0;
          img {
            width: calc(100% + 2pt);
            height: 110px;
            border-radius: 10px 10px 0 0;
            display: block;
          }
          .content {
            box-sizing: border-box;
            border: 1pt solid #e2dfe3;
            border-top: 0;
            border-radius: 0 0 10px 10px;
            padding: 10px 5px;
            .name {
              font-size: 15px;
              width: 100%;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
            }
            .flex {
              display: flex;
              align-items: center;
              padding: 10px 0;
              .left {
                flex: 2;
                font-size: 13px;
                color: #999;
              }
              .right {
                width: 60px;
                text-align: right;
                color: #9e1435;
                font-size: 13px;
              }
            }
          }
        }
      }
    }
  }
}
</style>