<template>
  <van-row class="grid">
    <van-col
      :span="span"
      v-for="(gridItem,index) in gridArray"
      :key="index"
      class="grid-item"
      @click.native="goRouter(gridItem)"
    >
      <img
        :src="pic =='1'?gridItem.icon:$utils.dealImg(gridItem.icon)"
        alt
        :style="`width:${width};height:${height};border-radius:${borderRadius}`"
      />
      <span :style="`color:${fontColor};margin-top: ${spanMargin}`">{{ gridItem.cateName }}</span>
      <p class="num" v-if="gridItem.num">{{gridItem.num}}</p>
    </van-col>
  </van-row>
</template>
<script>
import { Row, Col } from "vant";
export default {
  components: {
    [Row.name]: Row,
    [Col.name]: Col
  },
  props: {
    pic: {
      type: String,
      default: "2"
    },
    height: {
      type: String,
      default: "44px"
    },
    width: {
      type: String,
      default: "44px"
    },
    borderRadius: {
      type: String,
      default: ""
    },
    fontColor: {
      type: String,
      default: "#000"
    },
    gridArray: {
      type: Array,
      default: () => []
    },
    span: {
      type: String,
      default: "6"
    },
    spanMargin: {
      type: String,
      default: "10px"
    }
  },
  methods: {
    goRouter(obj) {
      this.$emit("goRouter", obj);
    }
  }
};
</script>
